.smooth-wrapper{
    clip-path: inset(80px 0px 0px 0px);
    @include windowMax1440p{
        clip-path: inset(74px 0px 0px 0px);
    }
}

.smooth-wrapper-active{
    clip-path: inset(200px 0px 0px 0px);
    @include windowMax1440p{
        clip-path: inset(184px 0px 0px 0px);
    }
}

.swap-wrapper{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}